import Weather from './Weather';
import s from './Main.module.css';
import searchIcon from './../img/icons/search-icon.svg'
import closeIcon from './../img/icons/close.svg'

const Main = (props) => {
    return(
        <div className = {s.main}>
            {props.isErr && 
            <div className = {s.err}> Вы не дали доступа к гео-данным! Воспользуйтесь поиском внизу! </div>
            }
            {props.currentGeo.temp && 
            <Weather 
            name = {props.currentGeo.name} 
            temp = {props.currentGeo.temp}
            weather = {props.currentGeo.weather}
            feels_like = {props.currentGeo.feels_like}
            icon = {props.currentGeo.icon}
            description = {props.currentGeo.description}
            setWeekForecast = {props.setWeekForecast}
            />
            }
            {props.locations.map( location =>  
                <Weather
                deleteCity = {props.deleteCity}
                name = {location.name} 
                temp = {location.temp}
                weather = {location.weather}
                feels_like = {location.feels_like}
                icon = {location.icon}
                setWeekForecast = {props.setWeekForecast}
                description = {location.description} />
            )}

            {props.isSearching 
            ? <button className = {s.modalButton} onClick = { () => props.setIsSearching(false) }>
                <img src = {closeIcon} alt="closeIcon"/>
              </button>
            : <button className = {s.modalButton} onClick = { () => props.setIsSearching(true) } >
                <img src = {searchIcon} alt="searchIcon"/>
              </button>}
        </div>
    )
}

export default Main;